p{
    margin: 0;
}

.profile{
    display: flex;
    flex-direction: column;
    border: 1px solid gainsboro;
    border-radius: 10px;
    width: 300px;
    background-color: white;
    overflow: hidden;
    margin-top: 20px;
}

.description{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
}

.avatar{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin-top: 40px;
}

.name, .quantity{
    font-weight: 600;
}

.tag, .location, .label{
    color: grey;
}

.location{
    margin-bottom: 40px;
}

.stats{
    list-style: none;
    display: flex;
    flex-direction: row;
    padding: 0;
    background-color: aliceblue;
    margin: 0;
}
.statsList{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: calc(100% / 3);
    text-align: center;
    padding: 20px 0;
}